
import renderMenu from '../global/menu';

import {swiperInit} from '../global/swiper';
// import { changeBgColor } from '../logic/change-bg-color';

import testimonialSliderContent from '../../../../testimonial-slider-content.json'; 

// import '../../../css/src/style.scss';

// // import SmoothScroll from 'smoothscroll-for-websites';

// const edfxBg = document.querySelector('.edfx__bg');

import footerLinkAnimation from '../effects/footer-link-animation';

import swapBGMenuSlide from '../effects/swapBGMenuSlide';

console.log(testimonialSliderContent);

// console.log(JSON.parse(testimonialSliderContent));

const swiperContentArray = testimonialSliderContent.testimonials.reduce((acc, curr, currIndex) => {

    const { company, content} = curr;

    const swiperContentWrapperObject = {
        slideContent: {company, content}, 
        slideId: `swiper-slide-0${currIndex + 1}`, 
        slideClass: `swiper-slide`
    };

    acc.push(swiperContentWrapperObject);

    return acc;

}, []);

swiperInit({swiperSlides: swiperContentArray});

renderMenu();

footerLinkAnimation();

swapBGMenuSlide(2000);

